<template>
    <div class="login-container">
        <div class="login-form-container">
            <form id="loginForm" @submit="handleSubmit" novalidate autocomplete="off">
                <h3>LOGIN</h3>

                <div v-if="errors.length" class="error-box">
                    <ul>
                        <li v-for="error in errors" :key="error">{{ error }}</li>
                    </ul>
                </div>

                <div class="form-group">
                    <input type="email" id="uEmail" name="uEmail" class="form-control" placeholder="電子郵件地址"
                        v-model="loginObj.email" />
                </div>

                <div class="form-group">
                    <div class="input-pass">
                        <input :type="pwdShow ? 'text' : 'password'" name="uPass" placeholder="密碼" id="uPass"
                            class="form-control" v-model="loginObj.pass" />
                            <i :class="pwdShow ? 'fas fa-eye-slash fa-2xl' : 'fas fa-eye fa-2xl'" v-if="showPwdIcon && loginObj.pass.length >0" @click="toggleShowPwd"></i>
                    </div>
                </div>

                <div class="form-group">
                    <input type="submit" value="登入" class="btn">
                    <p>還沒擁有帳戶? <router-link @click="scrollToTop()" to="/register">註冊
                        </router-link>
                    </p>
                </div>
            </form>
        </div>
    </div>
</template>


<script>
import axios from "axios";
import { mapMutations } from "vuex";
export default {
    name: 'Login',

    data() {
        return {
            loginObj: { email: "", pass: "" },
            matchUser: undefined,
            errors: [],
            pwdShow: false,
        }
    },

    methods: {
        ...mapMutations(["setUser"]),

        scrollToTop() {
            window.scrollTo(0, 0);
        },

        toggleShowPwd() {
                this.pwdShow = !this.pwdShow;
        },

        showPwdIcon() {
            this.showPwdIcon = true;
        },

        async getMatchUser(email) {
            let data = await axios.get('/users/' + email);
            this.matchUser = data.data;
        },

        async handleSubmit(e) {
            this.errors = [];

            if (!this.loginObj.email) {
                this.errors.push("請輸入電子郵件地址");
            }
            else {
                if (!/^[a-zA-Z0-9.!#%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*/.test(this.loginObj.email)) {
                    this.errors.push('請輸入有效的電子郵件地址');
                }
            }


            if (!this.loginObj.pass) {
                this.errors.push('請輸入密碼');
            } else {
                if (!/[!@#$%^&*]/.test(this.loginObj.pass)) {
                    this.errors.push('密碼必須包含至少一個特殊字元');
                }

                if (this.loginObj.pass.length < 8) {
                    this.errors.push('密碼必須大於8個字元');
                }
            }

            if (!this.errors.length == 0) {
                e.preventDefault();
            }
            else {
                e.preventDefault();
                await this.getMatchUser(this.loginObj.email);
                if (!this.matchUser) {
                    this.errors.push("電子郵件地址或密碼有誤!")
                }
                else {
                    if (this.matchUser.user_password === this.loginObj.pass) {
                        this.matchUser.user_password = "";
                        this.setUser(this.matchUser);
                        this.$router.push("/menu");
                    }
                    else {
                        this.errors.push("電子郵件地址或密碼有誤!")
                    }
                }
            }
        },

        async checkAuth() {
            const token = localStorage.getItem('authToken');
            if (token) {
                try {
                    const response = await axios.get('/protected', {
                        headers: { Authorization: `Bearer ${token}` }
                    });
                    this.setUser(response.data.user);
                    this.$router.push("/menu");
                } catch (error) {
                    console.error('Auth check failed:', error);
                }
            }
        }

    }

}
</script>

<style scoped>
.login-container {
    padding: 2rem 9%;
}

.login-container .login-form-container {
    background-color: #fff;
    height: 90vh;
}

.login-container .login-form-container form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 40rem;
    width: 100%;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.05);
    border: 0.1rem solid rgba(0, 0, 0, 0.2);
    padding: 2rem;
    border-radius: .5rem;
    animation: fadeUp .4s linear;
}

.login-container .login-form-container form h3 {
    padding-bottom: 1rem;
    font-size: 2rem;
    font-weight: bolder;
    text-transform: uppercase;
    color: #130f40;
    margin: 0;
}

.login-container .login-form-container form .form-control {
    margin: .7rem 0;
    border-radius: .5rem;
    background: #f7f7f7;
    padding: 2rem 1.2rem;
    font-size: 1.6rem;
    color: #130f40;
    text-transform: none;
    width: 100%;
    border: none;
}

.login-container .login-form-container form .btn {
    margin-bottom: 1rem;
    margin-top: 1rem;
    width: 100%;
}

.login-container .login-form-container form p {
    padding-top: 1rem;
    font-size: 1.5rem;
    color: #666;
    margin: 0;
}

.login-container .login-form-container form p a {
    color: #27ae60;
}

.login-container .login-form-container form p a:hover {
    color: #130f40;
    text-decoration: underline;
}

.login-container .login-form-container form .error-box {
    background-color: #fff9fa;
    box-sizing: border-box;
    border: 2px solid rgba(255, 66, 79, .2);
    border-radius: 2px;
    font-size: 12px;
    margin-bottom: 20px;
}

.login-container .login-form-container form .error-box ul {
    list-style-type: none;
    margin: 0;
    padding: 10px 0px;
}

.login-container .login-form-container form .error-box ul li {
    padding-left: 10px;
    color: rgb(182, 0, 0);
}

.input-pass {
    position: relative;
    width: 100%;
}

.input-pass input {
    width: calc(100% - 40px);
    padding-right: 40px; 
}

.input-pass i {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    cursor: pointer;
}
</style>